 isUploadProgess(){
  return this.uploadResponse.status == "progress" ? true : false;
}

isUploadCompleted(){
  return this.uploadResponse.status == "completed" ? true : false;
}

isAssetImageNotAvailable(){
  return this.asset.assetImageId == null ? true : false;
}

  uploadFile(event){

    let file = event[0];
    let userId = parseInt(this.cookieService.get('userId'))

    this.fileUploadService.upload(file, userId).subscribe((res:any) => {

      if(res != undefined){
        this.uploadResponse = res;
      }

      if(this.isUploadCompleted()){

        this.asset.assetImageId = this.uploadResponse.fileId

        let newParams = {
          fileDetailsId: this.uploadResponse.fileId, 
          apartmentId: Number(this.cookieService.get('apartmentId'))
        }

        this.fileDetailsService.getFileDetailsById(newParams).subscribe((res:any) => {
          this.filePath = res[0].filePath;
          this.downloadFile(this.filePath)
        })

      }

    })

  }

  downloadFile(filePath) {

    this.fileDownloadService.downloadFile(filePath).subscribe((res:any) => {

      this.isFileDetailsAvailable = true;

      let splitFile = filePath.split('.')
      this.isImageUploaded = this.sharedService.imageFormats.includes(splitFile[1]);

      const blob = res.body
      let objectURL = URL.createObjectURL(blob); 
      let sanitizeUrl:any = this.sanitizer.bypassSecurityTrustUrl(objectURL);
      this.fileUrl = sanitizeUrl
      
    })

  }

  deleteFile(){


    let details = {
      "fileDetailsId": this.asset.assetImageId,
      "fileName": this.filePath,
      "filePath": this.filePath,
      "fileSize": 0,
      "description": this.filePath,
      "isActive": true,
      "insertedBy": parseInt(this.cookieService.get('userId')),
      "insertedOn": new Date().toISOString(),
      "updatedBy": null,
      "updatedOn": null,
      "apartmentId": parseInt(this.cookieService.get('apartmentId'))
    }

    let params = {
      fileDetails: details
    }

    this.fileDetailsService.deleteFileDetails(params).subscribe((res:any) => {
      this.isFileDetailsAvailable = false;
      this.uploadResponse = { status: '', message: '', fileId: null };
      this.asset.assetImageId = this.uploadResponse.fileId
    })

  }

  bytesToSize(bytes) {
   if(bytes < 1024) return bytes + " Bytes";
    else if(bytes < 1048576) return(bytes / 1024).toFixed(3) + " KB";
    else if(bytes < 1073741824) return(bytes / 1048576).toFixed(3) + " MB";
    else return(bytes / 1073741824).toFixed(3) + " GB";
  }

