  ngOnInit() {

    this.uploadResponse = { status: '', message: '', fileId: null };
  	this.asset = {};
    this.asset.assetCategoryId = "";
    this.asset.assetConditionId = "";
    this.asset.vendorId = "";
    this.asset.assetLocation ="";
    this.asset.depreciationMethodId="";
    this.asset.assetSubcategoryId="";
    this.asset.assetImageId = null;
    this.getAllLocation();
    this.getDepreciatonList();
    this.getAllCategory();

    if(this.route.params['value'].id != undefined) {
      this.isEditAsset = true;

      let params = {
        apartmentId: Number(this.cookieService.get('apartmentId')),
        assetId: this.route.params['value'].id
      }

      this.assetService.getAllAssetByAssetId(params).subscribe((res:any) => {
        
        this.asset = res[0];
        
        let newParams = {
          fileDetailsId: this.asset.assetImageId, 
          apartmentId: Number(this.cookieService.get('apartmentId'))
        }

        this.fileDetailsService.getFileDetailsById(newParams).subscribe((res:any) => {
          this.filePath = res && res.length >0 ?res[0].filePath:'';
          this.downloadFile(this.filePath)
        })

      })
      let reqInsuranceParam :any = {};
      this.currentAssetId =params.assetId;
      reqInsuranceParam.AssetId = params.assetId;
      this.assetService.getAllAssetInsurancePlanByAssetId(reqInsuranceParam).subscribe((res:any) => {
        if(res && res.length>0 ){
        this.insurance = res[0]
      } else {
        this.isAddInsurance = true;
        this.insurance={};
        debugger;
      }
      });
    }

    let assetCategoryParams = {
      LookupTypeId: 19
    }

    this.lookupService.getLookupValueByLookupTypeId(assetCategoryParams).subscribe((res:any) => {

      this.assetCategoryData = res.filter(item => {
        return item.isActive;
      });

    });  

    let vendprParams = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

    this.vendorService.getVendorByApartmentId(vendprParams).subscribe((res:any) => {

      this.vendorDataList = res.filter(item => {
        return item.isActive;
      });

    });

    let assetStatusParams = {
      LookupTypeId: 64
    }

    //maintenance status 
    this.lookupService.getLookupValueByLookupTypeId(assetStatusParams).subscribe((res:any) => {
      this.statusTypeData = res;
    },
    error => {
      
    });

  }
