import { Component, OnInit, Inject, Injector, ViewEncapsulation } from '@angular/core';

import { AssetService } from 'src/app/api/controllers/Asset';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from '../../../../shared/services/shared.service';
import { ModalService } from '../../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import * as _ from 'underscore';
import { AssetsAddSetupComponent } from 'src/app/shared/components/assets-add-setup/assets-add-setup.component';

@Component({
	selector: 'app-assets-setup',
	templateUrl: './assets-setup.component.html',
	styleUrls: ['./assets-setup.component.scss'],
	// encapsulation: ViewEncapsulation.None,
})
export class AssetsSetupComponent implements OnInit {
	modalService: any;
	public mainType: any = [];
	public tempMainType: any = [];
	isAssetLoaded: boolean = false;
	public searchCategory: any;
	public categoryList: any = [];
	public tempCategoryList: any = [];
	delType: any;
	currIndex: number;
	mainCurrIndex: number;
	selectedTab: any;
	depreciatonData:any =[];
	constructor(public dialog: MatDialog,
		private lookupService: LookupService,
		// private sharedService:SharedService,
		// private route: ActivatedRoute,
		private sharedService: SharedService,
		private cookieService: CookieService,
		private injector: Injector,
		private assetService: AssetService, ) {
		this.modalService = this.injector.get(ModalService);
	}
	
	ngOnInit() {
		this.getAllCategory();
		this.getAllMaintenance();
		this.getDepreciatonList();
		this.selectedTab ='assetSetup'
		this.sharedService.unitlistdeleteindexcast.subscribe(id => {
			if (id != null) {
				let param:any ={};
				param = {
					categoryId: id,
					deletedBy: parseInt(this.cookieService.get('userId'))
				}
				this.lookupService.deleteCategory(param).subscribe((res: any) => {
					// _.each(this.categoryList, (type) => {
					// 	if (type.id == id) {
					// 		type.isActive = false;
					// 	}
					// })
					this.getAllCategory();
					this.getAllMaintenance();
					setTimeout(() => {
						if (this.delType == "maintenance") {
							this.sharedService.setAlertMessage("Maintenance type deleted successfully");
						} else {
							this.sharedService.setAlertMessage("Asset Category deleted successfully");
						}

						this.sharedService.setUnitListDeleteIndex(null);
					}, 500)
				}, error => {
					console.log(error);
				});
			}
		});
	}

	addSetUp(name, type, value, e): void {
		// let data:any={};
		// data.name=type;
		console.log(type, value);
		const dialogRef = this.dialog.open(AssetsAddSetupComponent, {
			width: '70%',
			data: {
				'name': name,
				'type': type,
				'value': JSON.stringify(value)
			}
		});

		dialogRef.afterClosed().subscribe(result => {
			console.log('The dialog was closed');
			console.log(result);
			if (result) {
				this.getAllCategory();
				this.getAllMaintenance();
			}
		});
	}


	deleteCategory(id, type) {
		this.delType = type;

		this.modalService.showConfirmModal(id);
	}


	deleteMaintype(index) {
		//  this.mainType.splice(index,1)
	}
	deleteCategoryType(index) {
		this.categoryList.splice(index, 1)
	}

	// Tab
	showTab(type) {
		this.selectedTab = type;
	}
	// Tab
	isActive(type) {
		return this.selectedTab == type ? true : false;
	}

	filterCategoryItem(value) {
		this.categoryList = this.tempCategoryList.filter((item: any) => item.name.toLowerCase().indexOf(value.toLowerCase()) > -1
		);
	}
	filterMaitenanceItem(value) {
		this.mainType = this.tempMainType.filter((item: any) => item.name.toLowerCase().indexOf(value.toLowerCase()) > -1
		);
	}


	editSubCategory(data) {
		let reqObj: any = {};
		reqObj.id = data.id;
		reqObj.apartmentId = parseInt(this.cookieService.get('apartmentId'));
		reqObj.name = data.name;
		// if (data && data.subCategory.length > 0) {
		// 	this.subList.filter(val => {
		// 		val.id = val.id ? val.id : 0;
		// 	});
		// }
		reqObj.subCategory = data && data.subCategory.length > 0 ? data.subCategory : [];
		reqObj.isActive = true,
		reqObj.insertedBy = parseInt(this.cookieService.get('userId'));
		reqObj.insertedOn = new Date();
		reqObj.updatedBy = parseInt(this.cookieService.get('userId'));
		reqObj.updatedOn = new Date();
		let params :any ={};
		params.model = reqObj;
        params.subcategoryLookupTypeId = 68;
		this.lookupService.upsertSubCategory(params).subscribe((res: any) => {
			if (res) {
				this.sharedService.setAlertMessage("Sub Category updated successfully");
				this.isAssetLoaded = false;
				this.getAllCategory();
				this.currIndex = -1;
			}

		});
	}

	
	getAllMaintenance() {
		let queryParamBase:any={};
		queryParamBase = {
	   apartmentId: this.cookieService.get('apartmentId'),
	   lookupTypeId: 167,
	   subCategoryLookupTypeId: 168,
	 };
		 this.isAssetLoaded = true;
		 this.lookupService.getSubcategory(queryParamBase).subscribe((res: any) => {
			if (res) {
				this.isAssetLoaded = false;
				this.mainType = res ? res : [];
				this.tempMainType = this.mainType;
				// if (this.data && this.data.type === 'addSubType') {
				// 	this.setup.category = this.data && this.data.value && this.data.value.id ? this.data.value.id : '';
				// 	this.setup.categoryName = this.data && this.data.value && this.data.value.name ? this.data.value.name : '';
				// 	// console.log(this.category);
				// }
			}
		});
	}

	/**
	 * 
	 * @param get all category  
	 */

	getAllCategory() {
		let queryParamBase:any={};
       queryParamBase = {
      apartmentId: this.cookieService.get('apartmentId'),
      lookupTypeId: 19,
      subCategoryLookupTypeId: 68,
    };
		this.isAssetLoaded = true;
		this.lookupService.getSubcategory(queryParamBase).subscribe((res: any) => {
			if (res) {
				this.isAssetLoaded = false;
				this.categoryList = res ? res : [];
				this.tempCategoryList = this.categoryList;
			}
		});
	}


	editMaintenance(data) {
		let reqObj: any = {};
		reqObj.id = data.id;
		reqObj.apartmentId = parseInt(this.cookieService.get('apartmentId'));
		reqObj.name = data.name;
		reqObj.subCategory = data && data.subCategory.length > 0 ? data.subCategory : [];
		reqObj.isActive = true,
		reqObj.insertedBy = parseInt(this.cookieService.get('userId'));
		reqObj.insertedOn = new Date();
		reqObj.updatedBy = parseInt(this.cookieService.get('userId'));
		reqObj.updatedOn = new Date();
		let params :any ={};
		params.model = reqObj;
        params.subcategoryLookupTypeId = 168;
		this.lookupService.upsertSubCategory(params).subscribe((res: any) => {
			console.log(res, res);
			if (res) {
				this.sharedService.setAlertMessage("Maintenance sub types updated successfully");
				this.isAssetLoaded = false;
				this.getAllMaintenance();
				this.mainCurrIndex = -1;
			}

		});
	}


	getDepreciatonList() {
		let queryParamBase: any = {};
		queryParamBase = {
			ApartmentId: this.cookieService.get('apartmentId'),
			LookupTypeId: 104,
		};		
		this.lookupService.getLookupValueByLookupTypeId(queryParamBase).subscribe((res: any) => {
			if (res) {
		 this.depreciatonData = res?res:[];
		 if(this.depreciatonData && this.depreciatonData.length){
			 this.depreciatonData.filter( val =>{
               val.isCheck = val.isDisabled == true ? false:true;
			 })
		 }
			}
		});
  }

  updateDepreciatonList(data) {
	console.log(data);
	let reqObj: any = {};
	reqObj = {
		"lookupValueId":data.lookupValueId,
		"lookupTypeId": 104,
		"lookupValueName": data.lookupValueName,
		"description": data.description,
		'apartmentId': parseInt(this.cookieService.get('apartmentId')),
		"isActive": true,
		"insertedBy": parseInt(this.cookieService.get('userId')),
		"insertedOn": new Date(),
		"updatedBy": parseInt(this.cookieService.get('userId')),
		"updatedOn": new Date(),
		"isDisabled": data.isCheck == false ? true : false
	};
	this.isAssetLoaded = true;
	let param :any ={};
	param.lookupvalue =reqObj;
	this.lookupService.updateLookupValue(param).subscribe((res: any) => {
		if (res) {
			this.sharedService.setAlertMessage("Depreciaton method  updated successfully");
			this.isAssetLoaded = false;
			this.getDepreciatonList();
		} else if (res.body.errorMessage) {
			// this.isError = true;
			// this.errorMessage = 'Not Added it already exist'
			this.isAssetLoaded = false;
			//  this.sharedService.setAlertMessage("Not Added as it already exist");
		}
	});
}
}

























