	/**
	 * get Asset list
	 * @param 
	 */
	getAsset() {
    let params :any ={};
    params.apartmentId = parseInt(this.cookieService.get('apartmentId'));

		this.assetService.getAllAssetByApartmentId(params).subscribe((res: any) => {
			if (res) {
				// this.assetList = res;
				if(res && res.length > 0 ){
					res.filter( val => {
						this.assetList.push({
						 'id': val.assetId, 
						'itemName': val.assetName });
					})
				}
			}
		});
	}



