	ngOnInit(): void {
		this.modalService = this.injector.get(ModalService);
		// delete maintenance
		this.sharedService.unitlistdeleteindexcast.subscribe(id => {
			if (id != null) {
				let param:any ={};
				param = {
					assetMaintenancePlanId: id,
					deleteBy: parseInt(this.cookieService.get('userId'))
				}
				this.assetService.deleteAssetMaintenancePlan(param).subscribe((res: any) => {
				//  this.getMaintenaceList();
				
				let pos = this.maintenanceList.map((e) => { return e.assetMaintenancePlanId; }).indexOf(id);
				 this.maintenanceList.splice(pos,1);
				 this.totalItems = this.maintenanceList.length;
				 this.ItemEndIndex = this.totalItems;

				//  if(this.totalItems>this.itemLimit){
				//    this.ItemEndIndex = this.itemLimit;
				//  }
				//  else {
				//    this.ItemEndIndex = this.totalItems;
				//  }
				
					setTimeout(() => {
						this.sharedService.setAlertMessage("Maintenance  deleted successfully");
						this.sharedService.setUnitListDeleteIndex(null);
					}, 500)
				}, error => {
					console.log(error);
				});
			}
		});
		this.route.paramMap.subscribe(params => {
			if (params) {
				this.assetId = params.get('id');
				let reqOBj = {
					assetId: this.assetId
				  }
				this.assetService.getAllAssetByAssetId(reqOBj).subscribe((res:any) => {
            		// this.asset = res[0];
					console.log('called')
					if(res && res[0]){
						this.maintenanceList =res;
					}
				});
			
			}
		})
		// this.search.assetId = 17;
		// this.getAllCategory();
		// this.getSubCategory();
		// this.getAsset();
		if(!this.assetId){
		this.getMaintenaceList();
		}
		this.assetSettings = {
			singleSelection: true,
			text: "Search Asset",
			selectAllText: 'Select All',
			unSelectAllText: 'UnSelect All',
			enableSearchFilter: true,
			badgeShowLimit: 3,
			// lazyLoading: true
		  };
		  this.categorySettings = {
			singleSelection: true,
			text: "Search Category",
			selectAllText: 'Select All',
			unSelectAllText: 'UnSelect All',
			enableSearchFilter: true,
			badgeShowLimit: 3,
			// lazyLoading: true
		  };
		  this.subCategorySettings = {
			singleSelection: true,
			text: "Search Sub Category",
			selectAllText: 'Select All',
			unSelectAllText: 'UnSelect All',
			enableSearchFilter: true,
			badgeShowLimit: 3,
			// lazyLoading: true
		  };
		  this.getAllCategory();
		  this.getAsset();
		  this.getSubCategory()
	}


