	getAllCategory() {
		let params :any ={}
		params.LookupTypeId = 19;
			this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {
		  console.log(res,'res:::::');
		  if (res) {
					// this.categoryList = res;
					if(res && res.length > 0 ){
						res.filter( val => {
							this.categoryList.push({
							 'id': val.lookupValueId, 
							'itemName': val.lookupValueName });
						})
					}
				}
	
			});
	  }
	  download(){
		let headerArray = ['assetTagNo','assetName','assetCategoryName',
		'assetSubcategoryName',	'actualMaintDate','maintenanceByCompany',
		'maintainedbyStaff','notes','maintLaborCost','maintPartsCost',
		'maintVat','maintWithTax','totalDiscount','maintenanceTotalBilledAmt',
	'plannedMaintenanceDate','nextPlannedMaintenance','alertBeforeDays']
	   this.csvService.downloadFile(this.maintenanceList,headerArray, 'manageMaintenance');
	 }
	 filterCategoryItem(value) {
		this.maintenanceList = this.tempMaintenanceList.filter((item: any) => 
		item.assetName.toString().toLowerCase().indexOf(value.toLowerCase()) > -1
	//   ||  item.assetCategoryName.toString().toLowerCase().indexOf(value.toLowerCase()) > -1
	//   ||  item.assetSubcategoryName.toString().toLowerCase().indexOf(value.toLowerCase()) > -1
		);
	}
	  getSubCategory() {
		let params :any ={}
		params.LookupTypeId = 68;
			this.lookupService.getLookupValueByLookupTypeId(params).subscribe((res: any) => {
			
				if (res) {
					// this.subCategoryList = res;
					if(res && res.length > 0 ){
						res.filter( val => {
							this.subCategoryList.push({
							 'id': val.lookupValueId, 
							'itemName': val.lookupValueName });
						})
					}
				}
			});
		}
	
		/**
		 * get Asset list
		 * @param 
		 */
		getAsset() {
		let params :any ={};
		params.apartmentId = parseInt(this.cookieService.get('apartmentId'));
	
			this.assetService.getAllAssetByApartmentId(params).subscribe((res: any) => {
				if (res) {
					// this.assetList = res;
					if(res && res.length > 0 ){
						res.filter( val => {
							this.assetList.push({
							 'id': val.assetId, 
							'itemName': val.assetName });
						})
					}
				}
			});
		}
	
	getMaintenaceList(){
		let params:any = {};
		params.apartmentId = parseInt(this.cookieService.get('apartmentId'));
		params.active = 1;
		 this.assetService.getAllAssetMaintenancePlanByApartmentIdStatus(params).subscribe((res:any) => {
		  if(res) {
          this.maintenanceList = res.length >0? res:[];
		  this.tempMaintenanceList = this.maintenanceList;
		  this.totalItems = this.maintenanceList.length;

		  if(this.totalItems>this.itemLimit){
			this.ItemEndIndex = this.itemLimit;
		  }
		  else {
			this.ItemEndIndex = this.totalItems;
		  }
		}
		  })
	  }
	  getIndexParams(event){

		this.ItemStartIndex = event.ItemStartIndex;
		this.ItemEndIndex = event.ItemEndIndex;
		this.itemLimit = event.itemLimit;
	  }
	
